body.popup {
  md-dialog,
  .md-dialog-container {
    box-shadow: none;
    border-radius: 0;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    max-width: unset;
    max-height: unset;
  }
  // HACK: work around window-resize while dialog open issues.
  .md-dialog-container {
    height: 100% !important;
    top: 0 !important;
  }

  md-dialog-content {
    flex: 1;
  }
}
