.recorder {
  md-toolbar {
    min-height: 48px;
    md-menu {
      padding: 0;
    }
  }
  .md-toolbar-tools {
    height: 48px;
    // negative last-child md-button margin not effective.
    padding-right: 2px;
  }

  .group-heading {
    > md-icon {
      margin-left: 0;
      margin-right: 8px;
    }
    margin-right: -8px;
    display: flex;
    flex-direction: row;
    align-items: center;
    @include md-subhead();
    line-height: 24px;
  }

  .ctrlButtons {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    justify-items: center;
    grid-gap: 0.2rem;

    #record-button,
    #upgrade-button {
      text-transform: none;
    }

    #record-button,
    #upgrade-button,
    .setup-tab-permissions {
      width: 70%;
      box-shadow: none !important;
      border-radius: 0.7rem;
      font-size: 1.7rem;
      font-weight: 900;
      &:not([disabled]) {
        background-color: #00aaff;
        color: white;
        &:hover {
          background-color: #0385c6 !important;
        }
      }
      &.md-primary {
        min-height: 48px;
      }
      &.big md-icon {
        font-size: 36px;
        width: 36px;
        height: 36px;
      }
    }

    .recordings-limit {
      padding-bottom: 0.4rem;
      font-size: 1.2rem;
      color: #8f8f8f;
    }
  }
  .free-info {
    @include md-body-1();
    color: $foreground-2;
    padding-left: 8px;
  }

  .tab-title {
    display: inline-block;
    max-width: 180px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: middle;
  }

  .desktop-choose-prompt {
    @include wb-absolute-full();
    font-size: 2rem;
    background: rgba(255, 255, 255, 0.9);
    z-index: 2;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    padding: 20px;
    .message {
      font-weight: 500;
    }
    .hint {
      margin-top: 20px;
      font-weight: 300;
    }
  }

  md-toolbar {
    .cf-mic-level {
      border: 1px solid rgba(255, 255, 255, 0.2);
    }
  }

  #recorder-wrapper {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto;
  }
}

cf-mic-level-icon-menu {
  md-menu > .md-button.md-icon-button {
    width: 48px;
    display: flex;
  }
  .cf-mic-level {
    vertical-align: middle;
    width: 6px;
    height: 24px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    margin: 0 0 0 -5px;
  }
}

.mic-option-menu {
  width: 288px;
  .cf-mic-level {
    width: 100%;
    height: 6px;
    border: 1px solid rgba(0, 0, 0, 0.1);
  }
  .mic-level-help {
    padding: 0 16px;
    color: rgba(0, 0, 0, 0.7);
  }
}

cf-recorder-recording {
  div.show-on-recording {
    > * {
      display: inline;
    }
    md-button {
      width: 10px;
    }
  }

  .setup-button {
    justify-self: center;
    font-weight: 900;
    border: 2px solid;
    border-radius: 0.7rem;
    width: 70%;
  }

  .recording-container {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: left;
    align-items: center;
    grid-gap: 0.5rem;
    grid-auto-rows: 1fr;
    padding-left: 20px;

    .option {
      display: grid;
      grid-template-columns: auto 1fr auto;
      grid-template-rows: auto;
      padding: 0;
      font-size: 1.5rem;
      align-content: center;
      min-height: 40px;
    }
  }

  .recording-checkbox {
    margin: 0;

    .md-bar {
      height: 21px;
      border-radius: 2rem;
      top: 2px;
    }

    .md-thumb {
      background-color: white;
      height: 1.5rem;
      width: 1.5rem;
      box-shadow: none;
      top: 3.4px;
      left: 4px;
    }
  }

  .recording-checkbox.md-checked {
    .md-bar {
      background-color: #ff8282;
    }

    .md-thumb {
      left: 1px;
    }
  }

  .topnav {
    min-height: 50px;

    * {
      color: white;
    }

    .md-toolbar-tools {
      display: grid;
      grid-template-columns: auto auto 1fr;
      grid-auto-flow: column;
      grid-gap: 0.2rem;
      padding: 0 0.5rem;
      height: 50px;

      .md-button.md-icon-button {
        margin: 0;
      }
    }

    * {
      color: white;
      font-weight: 900;
    }

    .md-icon-button {
      * {
        color: white;
        font-weight: 900;
        font-size: 2.5rem;
      }
    }
  }

  cf-auto-stop-dropdown .option {
    margin-left: 0;

    .option-label {
      font-size: 1.6rem;
    }

    button.md-button {
      min-height: 0px;
      min-width: 0px;
    }
  }

  .ctrlButtons .ctrl-buttons-row {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);

    button {
      display: grid;
      align-items: center;
      border: none;
      background-color: #ebebeb;
      height: 60px;
      border-radius: 0px;
      margin: 0;
      padding: 0;

      &:hover {
        background-color: #b9b9b9;
      }

      md-icon {
        font-size: 3rem;
        font-weight: 900;
        color: #555555;
        height: 30px;
        width: 30px;
      }

      &.stop-button {
        background-color: #d95a5a !important;

        * {
          color: white !important;
        }

        &:hover {
          background-color: #a22831 !important;
        }
      }
    }
  }
}

md-checkbox.recording-checkbox {
  margin-top: 10px;
  margin-bottom: 5px;
}

#restart-dialog,
#discard-dialog {
  padding: 5%;
  padding-bottom: 0;
  h2 {
    margin: 0;
  }
  md-dialog-content {
    overflow: hidden;
    padding: 0;
    p {
      margin-top: 0;
      font-style: italic;
      color: gray;
    }
  }
}

cf-recorder-loading {
  display: grid;
  justify-content: center;
  align-content: center;
  height: 200px;
}

cf-capture-source-info {
  span {
    font-size: 2rem;
  }
}

.help-link,
.delete-videos-link {
  color: #00aaff;
}

.delete-videos-link {
  margin: 2rem auto;
}

.reached-video-limit-text {
  font-family: 'Source Sans Pro';
  margin: auto;
  font-size: 26px;
  font-style: normal;
  font-weight: 700;
  line-height: 36px;
  letter-spacing: 0em;
  text-align: center;
  height: 72px;
  width: 262px;
  left: 32px;
  top: 147px;
}

.reached-video-limit-call-to-action {
  font-family: 'Source Sans Pro';
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 0em;
  text-align: center;
  height: 42px;
  width: 292px;
  left: 17px;
  top: 239px;
  margin: 1rem auto;
}
