@import '../colors';
@import '../functions';
@import '../typography';

.mat-form-field.mat-form-field-appearance-outline {
  width: 100%;

  .mat-form-field-wrapper {
    margin: 0;
    padding: 0 0 cf-unit(1) 0;
  }

  &.mat-form-field-has-label .mat-form-field-wrapper {
    padding: cf-unit(1) 0;
  }

  // Force removal of padding via no-padding class
  &.no-padding .mat-form-field-wrapper {
    padding: 0;
  }

  // Force removal of bottom padding via no-padding-bottom class
  &.no-padding-bottom .mat-form-field-wrapper {
    padding: cf-unit(1) 0 0 0;
  }

  .mat-form-field-flex {
    margin: 0;
    padding: 0;
  }

  .mat-form-field-outline {
    top: 0;
    // For box shadow
    border-radius: 5px;
  }

  .mat-form-field-outline-gap {
    display: none;
  }

  .mat-form-field-infix {
    border: none;
    padding: cf-unit(0.5);
  }

  // Higher specificity to override defaults
  &.mat-form-field-has-label .mat-form-field-label-wrapper {
    top: cf-unit(-1);
    height: cf-unit(1);
    padding: 0;
    pointer-events: auto;

    .mat-form-field-label {
      top: 0;
      width: 100%;
      margin: 0;
      @include cf-typography(caption);
      font-weight: 600;
      text-transform: uppercase;
      transform: none;
    }
  }

  // Lower specificity to keep dynamic color
  .mat-form-field-label {
    color: rgba(0, 0, 0, 0.64);
  }

  .mat-select-arrow-wrapper {
    transform: none;
  }

  .mat-form-field-suffix {
    top: 0;
  }

  .mat-form-field-subscript-wrapper {
    margin-top: 0.3rem; // tiny text needs tiny offset
    padding: 0;
  }

  textarea,
  input {
    font-weight: 300;
  }

  textarea {
    resize: none;
  }

  // Focus styles
  &.mat-focused {
    .mat-form-field-outline {
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    }

    .mat-form-field-outline-thick,
    .mat-form-field-label {
      color: $cf-blue;
    }
  }

  // Invalid styles
  &.mat-form-field-invalid {
    .mat-form-field-outline-thick,
    .mat-form-field-label {
      color: $cf-red;
    }
  }
}
