cf-topnav {
  &[popup-mode] {
    .md-toolbar-tools {
      h1 {
        font-size: inherit !important;
        letter-spacing: -0.05rem;
      }

      .md-button.md-icon-button * {
        font-size: 2.5rem;
        color: white;
      }
    }
  }
  md-toolbar {
    ng-transclude {
      min-width: 0;
      flex: 1;
      display: flex;
      flex-direction: row;
      align-items: center;
    }
    h1 {
      letter-spacing: -0.15rem;
      text-align: center;
      width: 100%;
      font-size: 3.5rem !important;
      white-space: nowrap;
      color: white;
      font-weight: 900 !important;
      overflow: hidden;
      text-overflow: ellipsis;
      min-width: 0;
    }
    .md-button.md-icon-button {
      width: 46px;
      height: 46px;
      min-width: 46px;
      min-height: 46px;

      md-icon {
        font-size: 3rem;
        color: white;
        height: 30px;
        width: 30px;
      }
    }
  }

  @media (max-width: 599px) {
    md-toolbar {
      min-height: 56px;
    }
    .md-toolbar-tools {
      height: 56px;
      max-height: 56px;
    }
  }

  @media (min-width: 600px) and (max-width: 959px) {
    md-toolbar {
      min-height: 64px;
    }
    .md-toolbar-tools {
      height: 64px;
      max-height: 64px;
    }
  }
}
