@import '../breakpoints';
@import '../functions';
@import '../mixins';
@import '../typography';

mat-card.mat-card {
  @include card-base;
  padding: 3rem 2.5rem;

  // Overrides material selector
  &:not([class*='mat-elevation-z']) {
    box-shadow: 0 0 15px 2px rgba(0, 0, 0, 0.1);
  }

  .mat-card-title {
    margin-bottom: cf-unit(0.5);
    @include cf-typography(title);
    line-height: cf-unit(1.5);
    text-align: center;
    @include dotted-rule;
  }

  .mat-card-content {
    @include cf-unit-margin(1);
  }

  .mat-button-base {
    display: block;
    width: 100%;
    max-width: cf-unit(9);
    @include center-margin;
  }

  .mat-card-actions {
    display: grid;
    grid-gap: cf-unit(0.5);
    margin-left: 0;
    margin-right: 0;
    padding: 0;

    //override default styles
    &,
    &:last-child {
      margin-bottom: 0;
    }

    @include sm-min {
      // Single right-aligned row for larger screens
      grid-auto-flow: column;
      justify-content: end;
    }
  }

  .mat-divider-horizontal {
    position: static;
    max-width: cf-unit(8);
    // negative margin to have effectively '0' height
    margin: -1px auto;
    border-top-width: 2px;
  }
}
