md-icon.png-icon {
  $icons: drive, youtube, newNotification;
  $path: '/assets/images/png-icons';
  margin: auto;
  vertical-align: middle;
  display: inline-block;
  width: 24px;
  height: 24px;
  background: no-repeat center;
  background-size: contain;

  @each $icon in $icons {
    &.#{$icon} {
      background-image: url('#{$path}/#{$icon}.png');
    }
  }
}
