@import './cf-colors.scss';
@import './cf-typography.scss';
// can't import styles in from submits due to else it will interfere with pre-existing styles
// @import './submit-styles/material/_index.scss';

// Global styles for Angular Material components

// Dialog styling. cf-dialog class is added by default in CastifyMaterialAppModule
.cf-dialog {
  .mat-dialog-content {
    @include cf-typography(body-1);
  }

  .mat-dialog-actions {
    display: grid;
    grid-column-gap: 1rem;
    grid-auto-flow: column;
    justify-content: right;
    padding-top: 20px;
  }
}

// Used to add a fixed label to an outlined form field. See youtube-publish-dialog-component
// for an example.
.cf-labeled-form-field {
  display: grid;
  grid-gap: 0.3rem;

  div {
    font-weight: 700;
  }

  .mat-form-field-appearance-outline .mat-form-field-wrapper {
    margin: 0;
    padding-bottom: 0;
  }

  textarea.mat-input-element {
    padding: 0;
    font-weight: 300;
  }

  .mat-form-field-infix {
    border-top: none;
  }
}

// MatMenu styling
.mat-menu-panel {
  transform-origin: top !important;
  // Specific styles for the Youtube channel menu in youtube-publish-dialog
  &.cf-youtube-channel-menu {
    width: 402px;
    max-width: 402px;
  }

  // Specific styling for the privacy menu based on where it's being used
  &.privacy-menu-panel {
    &.youtube-publish-dialog {
      max-width: 402px;
      width: 402px;
    }

    &.details-drive-share {
      max-width: 272px;
      width: 272px;
    }
  }
}

// Helper classes for Angular-Material snack bars
.mat-snack-bar-container {
  font-weight: 600;

  &.cf2-error-snackbar {
    background: $cf-error;

    // Default styling for the X (close) button
    .mat-simple-snackbar-action .mat-button {
      color: white;
      font-size: 3rem;
    }

    // Smaller text if using a custom action
    &.custom-action .mat-simple-snackbar-action .mat-button {
      font-size: 1.5rem;
    }
  }

  &.cf2-success-snackbar {
    background: $cf-blue;
  }

  &.cf2-snackbar .mat-simple-snackbar {
    justify-content: center;
  }

  .mat-simple-snackbar {
    color: white;
  }
}

// MatSlider styling
//
// Optional sister classes:
// - small-slider: renders a smaller slider
// - gray-background: forces a gray background on the slider track and adds a box
//   shadow to the thumb (for use on components with white backgrounds)
.mat-slider.cf-slider {
  width: 100%;
  padding: 0;
  height: 36px;
  font-size: 12px;
  .mat-slider-wrapper {
    top: 1em;
    height: 1em;
    .mat-slider-track-wrapper {
      border-radius: 0.5em;
    }
    .mat-slider-track-wrapper,
    .mat-slider-track-background,
    .mat-slider-track-fill {
      top: 0;
      height: 100%;
    }
    .mat-slider-track-background {
      background: rgba(#fff, 0.4);
    }
    .mat-slider-thumb {
      bottom: -14px;
      right: -14px;
      width: 28px;
      height: 28px;
      border: none;
      background: #fff;
      transform: scale(1);
    }
  }

  &.small-slider {
    height: 20px;
    font-size: 6px;

    .mat-slider-wrapper .mat-slider-thumb {
      bottom: -1.5em;
      right: -1.5em;
      width: 3em;
      height: 3em;
    }
  }

  &.gray-background {
    .mat-slider-wrapper {
      .mat-slider-track-background {
        background: lightgray;
      }
      .mat-slider-thumb {
        box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.25);
      }
    }
  }
}

// MatTooltip styling
.cdk-overlay-container .mat-tooltip {
  font-size: 1.5rem;
  font-weight: bold;
  padding: 6px 12px;
}
