@import './breakpoints';
@import './functions';
@import './svg/dot';

@mixin cf-unit-margin($unit) {
  margin-top: cf-unit($unit);
  margin-bottom: cf-unit($unit);
}

@mixin cf-unit-padding($unit) {
  padding-top: cf-unit($unit);
  padding-bottom: cf-unit($unit);
}

@mixin center-margin() {
  margin-left: auto;
  margin-right: auto;
}

/** Share Styles **/

/**
 * CARD BASE
 * Adds basic max-width, margins, and border radius shared among
 * 'card-like' components
 */
@mixin card-base() {
  max-width: cf-unit(24);
  @include cf-unit-margin(0.5);
  @include center-margin;
  border-radius: 0;
  @include sm-min {
    @include cf-unit-margin(1);
  }
  @include md-min {
    border-radius: 10px;
  }
}

/**
 * DOTTED LINE DECORATOR
 * override with .selector::after { display: none; }
 */
@mixin dotted-rule() {
  &::after {
    display: block;
    width: 12 * 15px; // 12 dots @ 15px wide
    height: cf-unit(0.5);
    @include center-margin;
    margin-top: cf-unit(0.5);
    border: none;
    background-image: $svg-dot;
    // position background to fixed repeating pixel size
    background-position: left center;
    background-size: 15px 12px;
    background-repeat: repeat-x;
    opacity: 0.8;
    content: '';
  }
}
