cf-upgrade-button {
  margin: 6px 8px;
  display: inline-block;

  .md-button:not([disabled]) {
    margin: 0;
    width: 100%;
    height: 100%;

    color: white !important;
    background-color: #ff8282 !important;
    font-weight: 700;

    &:hover {
      background-color: #c85255 !important;
    }

    &.blue-bg {
      background-color: #00aaff !important;
      &:hover {
        background-color: #0385c6 !important;
      }
    }
  }
}
