cf-file-sync-icon {
  width: 24px;
  height: 24px;
  margin: auto;
  display: inline-block;
  vertical-align: middle;
}

cf-file-sync-info {
  .progress-bar {
    grid-column: span 3;
    padding-bottom: 10px;
  }

  cf-file-sync-icon {
    width: 24px;
    margin-top: 16px;
    margin-bottom: 12px;
    box-sizing: content-box;
  }

  md-list-item cf-file-sync-icon:first-child {
    margin-right: 32px;
  }
}
