@import '../breakpoints';
@import '../functions';
@import '../typography';

.cdk-overlay-container {
  @include md-max {
    // Fullscreen dialogs for smaller screens
    // NOTE: uses !important flags to override inline defaults
    .cf-submit-dialog {
      width: 100%;
      max-width: 100% !important;
      min-width: 0 !important;
      height: 100%;
      max-height: 100% !important;
      min-height: 0 !important;
    }
  }

  .mat-dialog-container {
    padding: cf-unit(1);
    // Only needs border radius for non-fullscreen (larger screens)
    @include md-min {
      border-radius: 12px;
    }
  }

  .mat-dialog-title {
    margin-bottom: cf-unit(1);
    @include cf-typography(title);
  }

  .mat-dialog-content {
    margin: 0;
    padding: 0;

    :first-child {
      margin-top: 0;
    }

    :last-child {
      margin-bottom: 0;
    }
  }

  .mat-dialog-actions {
    display: grid;
    grid-gap: cf-unit(0.5);
    margin: cf-unit(1) 0 0 0;
    padding: 0;

    @include sm-min {
      // Single right-aligned row for larger screens
      grid-auto-flow: column;
      justify-content: end;
    }

    // Override margins from material base styles
    .mat-button-base + .mat-button-base {
      margin-left: 0;
    }
  }
}
