/**
 * Common breakpoints
 */
$sm-breakpoint: 384px; // cf-unit(16)
$mid-breakpoint: 576px; // cf-unit(24)

/**
 * Mixins for breakpoints
 */

@mixin sm-min {
  @media screen and (min-width: $sm-breakpoint) {
    @content;
  }
}

@mixin sm-max {
  @media screen and (max-width: $sm-breakpoint) {
    @content;
  }
}

@mixin md-min {
  @media screen and (min-width: $mid-breakpoint) {
    @content;
  }
}

@mixin md-max {
  @media screen and (max-width: $mid-breakpoint) {
    @content;
  }
}
