// HACK: new style for angularjs component.
// TODO: refactor to somewhere else if reused
@mixin cf2-button {
  text-transform: none;
  border-radius: 0.7rem;
  font-size: 1.7rem;
  font-weight: 900;
}

.view > .options {
  md-content {
    padding: 8px;
  }

  .option-desc {
    display: block;
    padding-left: 36px;
    color: $foreground-2;
  }

  section {
    margin-top: 20px;
  }

  .policy-hint {
    &:before {
      content: '(';
    }
    &:after {
      content: ')';
    }
    font-size: 14px;
  }
}

.options {
  background-color: #ff8282;
  display: grid;
  align-content: start;

  .options-container {
    justify-self: center;
    display: grid;
    justify-items: center;
    min-width: 55%;
    max-width: 700px;

    .option-card {
      display: grid;
      grid-template-columns: auto;
      grid-gap: 1.5rem;
      background-color: white;
      border-radius: 0.5rem;
      width: 100%;
      margin: 15px 0;
      padding: 3% 5%;
      grid-auto-rows: auto;

      h2 {
        font-size: 2.5rem;
        font-weight: 900;
        text-align: center;
        margin: 0 0 10px 0;
      }

      md-switch {
        display: grid;
        grid-template-columns: auto 1fr;
        align-self: center;
        margin: 0;
        white-space: normal;
        line-height: inherit;
        height: unset;

        .md-bar {
          height: 21px;
          border-radius: 2rem;
          top: 2px;
        }

        .md-thumb {
          background-color: white;
          height: 1.5rem;
          width: 1.5rem;
          box-shadow: none;
          top: 3px;
          left: 4px;
        }

        .md-label {
          margin-left: 16px;
          font-size: 1.4rem;
        }
      }

      md-switch.md-checked {
        .md-bar {
          background-color: #ff8282;
        }

        .md-thumb {
          left: 1px;
        }
      }

      h3 {
        margin: 0 0 -3px 0;
        font-size: 2rem;
        font-weight: 400;
      }

      .switch-description {
        font-size: 1.4rem;
        div {
          opacity: 0.8;
        }
      }

      .quality-select {
        padding-left: 59px;
        line-height: inherit;
        text-transform: none;
      }

      .keyboard-shortcuts-link {
        font-size: 2rem;
        padding-left: 59px;
      }

      .reset-buton {
        margin: 0;
        background-color: #f44336;
        @include cf2-button;
      }
    }
  }

  cf-storage-option {
    md-radio-button {
      &.md-checked .md-off {
        border-color: #ff8282;
      }
      .md-on {
        background-color: #ff8282;
      }

      &.drive-button {
        margin-bottom: 1.5rem;
      }

      .md-container {
        left: 10px;
      }

      .md-label {
        margin-left: 59px;
        font-size: 1.4rem;
        div {
          opacity: 0.8;
        }
      }
    }
  }

  cf2-error-hint .error-hint {
    margin: 0;
  }
}

#reset-extension-dialog {
  color: rgba(79, 79, 79, 0.87);
  font-family: 'Helvetica', sans-serif;
  button.warn {
    background-color: #f44336;
    @include cf2-button;
  }
}
