@font-face {
  font-family: 'Source Sans Pro';
  font-style: normal;
  font-weight: 200;
  src: local('Source Sans Pro ExtraLight'), local('SourceSansPro-ExtraLight'),
    cf-font-url('source-sans-pro/SourceSansPro-ExtraLight.woff2')
      format('woff2');
}
@font-face {
  font-family: 'Source Sans Pro';
  font-style: italic;
  font-weight: 200;
  src: local('Source Sans Pro ExtraLight Italic'),
    local('SourceSansPro-ExtraLightItalic'),
    cf-font-url('source-sans-pro/SourceSansPro-ExtraLightItalic.woff2')
      format('woff2');
}
