  src: local('Source Sans Pro Italic'), local('SourceSansPro-Italic'),
    cf-font-url('source-sans-pro/SourceSansPro-Italic.woff2') format('woff2');
}
// HACK, font does not provide 500 weights, use 600 weights also for 500, since used in md-material in some places
// We should probably not use this font in the first place together with angular-material which relies on all
// font weights to be available.
@font-face {
  font-family: 'Source Sans Pro';
  font-style: normal;
  font-weight: 500;
  src: local('Source Sans Pro SemiBold'), local('SourceSansPro-SemiBold'),
