@import '~@angular/material/theming';
@import './cf-colors.scss';
@import './cf-typography.scss';
@import './cf-material-styles.scss';

// Imported from cf-typography.scss
@include mat-core($cf-typography);

// colors generated with http://mcg.mbitson.com
$cf-pink-palette: (
  50: #fff0f0,
  100: #ffdada,
  200: #ffc1c1,
  300: #ffa8a8,
  400: #ff9595,
  500: $cf-pink,
  600: #ff7a7a,
  700: #ff6f6f,
  800: #ff6565,
  900: #ff5252,
  A100: #ffffff,
  A200: #ffffff,
  A400: #fff7f7,
  A700: #ffdede,
  contrast: (
    50: $cf-gray,
    100: $cf-gray,
    200: $cf-gray,
    300: $cf-gray,
    400: $cf-gray,
    500: white,
    600: $cf-gray,
    700: $cf-gray,
    800: $cf-gray,
    900: $cf-gray,
    A100: $cf-gray,
    A200: $cf-gray,
    A400: $cf-gray,
    A700: $cf-gray,
  ),
);

$cf-blue-palette: (
  50: #e0f5ff,
  100: #b3e6ff,
  200: #80d5ff,
  300: #4dc4ff,
  400: #26b7ff,
  500: $cf-blue,
  600: #00a3ff,
  700: #0099ff,
  800: #0090ff,
  900: #007fff,
  A100: #ffffff,
  A200: #f2f8ff,
  A400: #bfdbff,
  A700: #a6cdff,
  contrast: (
    50: #000000,
    100: #000000,
    200: #000000,
    300: #000000,
    400: #000000,
    500: #ffffff,
    600: #ffffff,
    700: #ffffff,
    800: #ffffff,
    900: #ffffff,
    A100: #000000,
    A200: #000000,
    A400: #000000,
    A700: #000000,
  ),
);

$cf-primary: mat-palette($cf-pink-palette);
$cf-accent: mat-palette($cf-blue-palette);
$cf-warn: mat-palette($mat-red);
$cf-theme: mat-light-theme($cf-primary, $cf-accent, $cf-warn);

@function cf-light-theme-foreground($color) {
  @return (
    base: $color,
    divider: $black-12-opacity,
    dividers: $cf-pink,
    disabled: rgba($color, 0.38),
    disabled-button: rgba($color, 0.38),
    disabled-text: rgba($color, 0.38),
    hint-text: rgba($color, 0.38),
    secondary-text: rgba($color, 0.54),
    icon: rgba($color, 0.54),
    icons: rgba($color, 0.54),
    text: rgba($color, 0.87),
    slider-off: rgba($color, 0.26),
    slider-off-active: rgba($color, 0.38)
  );
}

$cf-gray-foreground: cf-light-theme-foreground($cf-gray);
$cf-theme-custom: map-merge(
  $cf-theme,
  (
    foreground: $cf-gray-foreground,
  )
);

@include angular-material-theme($cf-theme-custom);

html body {
  height: 100%;
  margin: 0;
}
