@import './font-helpers';

@font-face {
  font-family: 'Helvetica';
  font-style: normal;
  font-weight: 300;
  src: local('Helvetica Light'), local('helvetica-light'),
    font-url('helvetica/helvetica-light.woff2') format('woff2');
}
@font-face {
  font-family: 'Helvetica';
  font-style: normal;
  font-weight: 400;
  src: local('Helvetica Regular'), local('helvetica-regular'),
    font-url('helvetica/helvetica-regular.woff2') format('woff2');
}
@font-face {
  font-family: 'Helvetica';
  font-style: italic;
  font-weight: 300;
  src: local('Helvetica Light Italic'), local('helvetica-light-italic'),
    font-url('helvetica/helvetica-light-italic.woff2') format('woff2');
}
@font-face {
  font-family: 'Helvetica';
  font-style: normal;
  font-weight: 700;
  src: local('Helvetica Bold'), local('helvetica-bold'),
    font-url('helvetica/helvetica-bold.woff2') format('woff2');
}
