@import '../colors';
@import '../typography';

.mat-table {
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.12);

  .mat-row .mat-cell,
  .mat-header-row .mat-header-cell {
    text-align: center;

    &:first-of-type {
      text-align: left;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      max-width: cf-unit(15);

      // Left align the 'center' column
      .mat-sort-header-container {
        grid-template-columns: 0 auto auto;
      }
    }

    &:last-of-type {
      text-align: right;
    }
  }

  .mat-header-row .mat-header-cell {
    @include cf-typography(body-2);
    font-weight: 700;
    color: rgba(0, 0, 0, 0.72);
    text-align: center;

    // Tooltip icon
    .mat-icon {
      height: 14px;
      width: 14px;
      margin-left: 4px;
      font-size: 14px;
      vertical-align: -2px;
    }
  }

  .mat-sort-header-container {
    display: grid;
    grid-template-columns: 1fr auto 1fr;

    .mat-sort-header-content {
      grid-column: 2;
    }

    .mat-sort-header-arrow {
      grid-column: 3;
    }
  }

  .mat-row:first-child {
    box-shadow: 0 4px 4px -4px rgba(0, 0, 0, 0.24) inset;
  }

  a {
    text-decoration: underline;
  }
}
