@mixin wb-absolute-full() {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

@mixin wb-transform-z-hack() {
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}

@mixin keyframes($name) {
  @-webkit-keyframes #{$name} {
    @content;
  }
  @keyframes #{$name} {
    @content;
  }
}
