// not exporting this file via _index.scss because it only deals with the guided tour styles
// it must be imported in the global styles because of overlays and z-indexing

@import './cf-colors.scss';

$tour-zindex: 1081;
$tour-step-color: #ffffff;
$tour-text-color: rgba(79, 79, 79, 0.87);
$tour-next-button-color: $cf-blue;
$tour-next-button-hover: $cf-blue;
$tour-back-button-color: rgba(79, 79, 79, 0.75);
$tour-next-text-color: #ffffff;
$tour-next-text-hover: #ffffff;
$tour-skip-link-color: rgba(79, 79, 79, 0.87);
$tour-shadow-color: #4c4c4c;

body.tour-open {
  overflow: hidden;
}

@mixin tour-triangle($direction, $color: currentColor, $size: 2rem) {
  @if not index(top right bottom left, $direction) {
    @error 'Direction must be either `top`, `right`, `bottom` or `left`.';
  }

  $opposite-direction: top;

  @if $direction==top {
    $opposite-direction: bottom;
  }

  @if $direction==bottom {
    $opposite-direction: top;
  }

  @if $direction==right {
    $opposite-direction: left;
  }

  @if $direction==left {
    $opposite-direction: right;
  }

  width: 0;
  height: 0;
  content: '';
  z-index: 2;
  border-#{$opposite-direction}: $size solid $color;
  $perpendicular-borders: $size solid transparent;
  @if $direction==top or $direction==bottom {
    border-left: $perpendicular-borders;
    border-right: $perpendicular-borders;
  } @else if $direction==right or $direction==left {
    border-bottom: $perpendicular-borders;
    border-top: $perpendicular-borders;
  }
}

ngx-guided-tour.new-details-page-tour {
  .guided-tour-user-input-mask {
    z-index: $tour-zindex;
  }

  .guided-tour-spotlight-overlay {
    z-index: $tour-zindex + 1;
  }

  .tour-step {
    z-index: $tour-zindex + 2;

    &.tour-bottom,
    &.tour-bottom-right,
    &.tour-bottom-left {
      .tour-arrow::before {
        @include tour-triangle(top, $tour-step-color);
      }
    }

    &.tour-top,
    &.tour-top-right,
    &.tour-top-left {
      .tour-arrow::before {
        @include tour-triangle(bottom, $tour-step-color);
      }
    }

    &.tour-left {
      .tour-arrow::before {
        @include tour-triangle(right, $tour-step-color);
      }
    }

    &.tour-right {
      .tour-arrow::before {
        @include tour-triangle(left, $tour-step-color);
      }
    }

    .tour-block {
      color: $tour-text-color;
      background-color: $tour-step-color;
      box-shadow: 0px 11px 15px -7px rgba(0, 0, 0, 0.2),
        0px 24px 38px 3px rgba(0, 0, 0, 0.14),
        0px 9px 46px 8px rgba(0, 0, 0, 0.12);
      border-radius: 8px;
      padding: 3rem;
    }

    .tour-progress-indicator {
      padding-bottom: 0px;
      text-align: center;
    }

    .tour-title {
      font-family: 'Source Sans Pro';
      font-size: 34px;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: -0.17px;
      color: #4f4f4f;
      font-weight: 900 !important;
      text-align: center;
      padding: 0;
      margin-top: 15px;
      margin-bottom: 0;
    }

    .tour-content {
      font-family: Helvetica, sans-serif;
      font-size: 18px;
      font-weight: normal;
      color: #4f4f4f;
      line-height: 1.4;
      letter-spacing: 0;
      min-height: 0px;
      padding: 0;
      text-align: center;

      .tour-content-link {
        text-decoration: underline;
        font-size: 14px;
        line-height: 16px;
        color: #676767;

        &:hover {
          color: $cf-blue;
        }
      }
    }

    .tour-buttons {
      padding: 40px 0 8px;
      position: relative;

      .skip-button {
        display: none;
      }

      .back-button {
        color: $tour-back-button-color;
        font-size: 16px;
        font-weight: 700;
        height: 48px;
        width: 120px;
        padding: 0px 16px;
        border: 0px;
        font-family: Helvetica, sans-serif;
        position: absolute;
        top: 50%;
        left: 0;
      }

      .next-button {
        background-color: $tour-next-button-color;
        color: $tour-next-text-color;
        font-size: 16px;
        font-weight: 700;
        border-radius: 4px;
        margin: 0px;
        padding: 0px 16px;
        border: 0px;
        font-family: Helvetica, sans-serif;
        width: 143px;
        height: 44px;

        &:hover {
          background-color: #3f8ed3;
          color: #d9d9d9;
        }
      }
    }
  }
}

