.cf-record-options {
  display: grid;

  cf-device-chooser {
    max-width: 120px;
    md-input-container {
      display: block;
    }
  }
  .option.cam cf-device-chooser {
    max-width: 200px;
    margin-left: 8px;
  }
  .option {
    padding-left: 36px;
    min-height: 48px;
    display: flex;
    flex-direction: row;
    align-items: center;
    md-input-container {
      padding-bottom: 4px;
      margin: 0 0 0 8px;
    }
    &.multi {
      display: block;
      > [layout='row'] {
        align-items: center;
      }
    }

    md-menu {
      padding: 0;
      > .md-button {
        min-width: unset;
        margin: 0;
      }
    }

    md-radio-button,
    md-checkbox {
      margin-top: 0;
      margin-bottom: 0;
      &:first-child {
        margin-left: -29px;
      }
    }
  }

  .option-label {
    white-space: nowrap;
    padding-right: 2px;
  }

  .cam-setup-hint {
    padding: 0;
  }

  .md-button.lower {
    text-transform: none;
  }

  button.cf-nav-button {
    margin: 0;
  }

  .more-option-button {
    margin: 0;
    width: 100%;
  }

  .popup-top-nav {
    min-height: 50px;

    * {
      color: white;
    }

    .md-icon-button {
      * {
        font-weight: 900;
        font-size: 25px;
      }
    }

    .md-toolbar-tools {
      display: grid;
      grid-template-columns: 1fr;
      grid-auto-flow: column;
      grid-gap: 3px;
      padding: 0 7.5px 0 3px;
      height: 50px;

      &.advanced-settings {
        grid-template-columns: auto 1fr;

        h2 {
          font-weight: 900;
        }
      }

      * {
        margin: 0;
      }

      &.auto-share-hint {
        height: 40px;
        justify-items: right;

        span {
          max-width: 200px;
          font-size: 1.5rem;
          font-weight: 900;
        }
      }
    }
  }

  md-tabs {
    md-tabs-canvas {
      height: 7rem;
    }

    md-pagination-wrapper {
      height: 100%;
      width: 100%;
      display: grid;
      grid-template-columns: repeat(3, minmax(30%, 1fr));
      grid-template-rows: 1fr;
      align-items: center;

      .md-tab {
        display: grid;
        align-content: center;
        grid-gap: 0.5rem;
        height: 100%;
        padding: 0;
        text-transform: none;

        .tab-label-icon {
          margin: 0;
          font-size: 35px;
          height: auto;
          width: auto;
        }

        .tab-label-text {
          font-weight: 700;
          margin-top: -0.8rem;
          font-size: 1.3rem;
        }
      }

      .md-tab.md-active {
        background-color: #f4f4f4;
      }
    }
  }

  .controls-container {
    display: grid;

    .slide-toggle {
      font-size: 1.5rem;
      font-style: italic;
      justify-self: center;
      padding: 1rem 0;
    }

    .setup-button {
      font-size: 1.3rem;
      justify-self: center;
      font-weight: 900;
      border: 2px solid;
      border-radius: 0.7rem;
      width: 70%;
    }

    .more-options {
      display: grid;
      padding: 8px;

      md-divider {
        width: 40%;
        justify-self: center;
      }

      .option {
        grid-template-columns: auto 1fr;

        md-input-container {
          max-width: 120px;
        }

        md-select {
          margin: 0;
          font-size: 1.4rem;

          .md-select-icon:after {
            font-size: 1rem;
          }
        }
      }
    }

    .option.cam > .cam-label {
      font-weight: 700;
      margin-left: 44px;
    }

    .option.no-mic {
      font-weight: 700;
      margin-left: 44px;
    }
  }

  cf-mic-option {
    width: 100%;
  }

  .option {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-template-rows: auto;
    padding: 0;
    font-size: 1.5rem;
    align-content: center;
    min-height: 40px;

    md-switch {
      margin: 0;

      .md-bar {
        height: 21px;
        border-radius: 20px;
        top: 2px;
      }

      .md-thumb {
        background-color: white;
        font-size: 15px;
        height: 15px;
        width: 15px;
        box-shadow: none;
        top: 3px;
        left: 4px;
      }
    }

    md-switch.md-checked {
      .md-bar {
        background-color: #ff8282;
      }

      .md-thumb {
        left: 1px;
      }
    }

    .md-label {
      font-weight: 700;
    }

    cf-device-chooser {
      justify-self: right;

      md-select {
        font-size: 1.4rem;

        .md-select-icon:after {
          font-size: 1rem;
        }
      }
    }

    cf-mic-level-icon-menu {
      .cf-mic-level {
        margin: 0;
      }
    }
  }

  md-icon path {
    // workaround for webcam position md-icon color
    color: rgba(0, 0, 0, 0.54);
  }

  .advanced-settings-content {
    padding-left: 16px;

    .option {
      grid-template-columns: auto 1fr;

      .option-label {
        font-weight: 700;
      }

      md-menu {
        justify-self: right;
      }

      .md-button {
        text-transform: none;
      }
    }

    cf-auto-stop-dropdown .option {
      margin-left: 0;
    }
  }
}

.video-count-limit-banner {
  background-color: #f4f4f4;
  color: #3b3b3b;
  font-size: 12px;
  line-height: 18px;
  display: flex;
  flex-direction: row;
  justify-content: start;
  padding: 1rem;
  padding-left: 0;
  align-items: center;
  .info-icon-button {
    max-width: 52px;
    min-width: 52px;
  }
}

.video-count-limit-banner-over-10-remaining-videos {
  margin: auto;
}

.video-count-limit-banner > md-icon {
  color: #3b3b3b;
  margin: 0 1rem 0 0;
}

.video-count-limit-banner a {
  color: #00aaff;
}
