.item-chooser-menu {
  display: inline-block;
  padding: 0;
  ng-transclude > .md-button {
    margin: 0;
  }
}

.item-chooser-menu-content,
.item-chooser-menu {
  > .md-button {
    text-transform: none;
  }
  .item-icon {
    margin: auto 16px auto 0;
    width: 24px;
    height: 24px;
    background-size: contain;
    display: inline-block;
    background-repeat: no-repeat;
    vertical-align: middle;
  }
}

.item-chooser-menu-content {
  min-width: 300px;
}

.item-chooser-menu-content .item-icon {
  margin-right: 32px;
}
