@import '../functions';
@import '../mixins';

.mat-button-base {
  // All buttons, except icon-buttons
  &.mat-button,
  &.mat-flat-button,
  &.mat-raised-button,
  &.mat-stroked-button {
    border-radius: 7px;
    padding: cf-unit(0.5) cf-unit(1);
    line-height: cf-unit(1);

    // Hack to darken button on hover (since ripple is present but disabled)
    .mat-button-ripple {
      background: rgba(0, 0, 0, 0);
      transition: background 0.3s ease-in-out;
    }

    &:not([disabled]):hover .mat-button-ripple {
      background: rgba(0, 0, 0, 0.1);
    }
  }

  // Custom shadow for raised button
  &.mat-raised-button {
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
  }
}
