.mic-level-slider {
  position: relative;
  .mic-level-tick {
    top: 4px;
    z-index: 0;
    // hack for datalist at 0 (with max = 5 and min = -2 -> range = 7)
    left: calc(2 / 7 * 100% + 2px);
    content: '';
    display: block;
    background: #c8c8c8;
    position: absolute;
    width: 2px;
    height: 14px;
  }
  > input {
    z-index: 1;
    -webkit-appearance: none;
    width: 100%;
    // height: 1px;
    border: 1px solid transparent;
    border-bottom: none;
    border-top: none;
    height: 10px;
    background: transparent;
    outline: none;

    &::-webkit-slider-runnable-track {
      z-index: 1;
      background: #c8c8c8;
      height: 2px;
    }
    &::-webkit-slider-thumb {
      z-index: 1;
      cursor: pointer;
      margin-top: -6px;
      width: 14px;
      height: 14px;
      -webkit-appearance: none;
      background: #ff8282;
      border-radius: 100%;
      box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
    }
  }
}
