/* You can add global styles to this file, and also import other style files */

// import old ajs styles
@import './ajs/main.scss';

// Isolate old styles so it does not interfere with submit styles.
body:not(.submit-styled) {
  @import './cf-styles/cf-material-theme.scss';
  @import './popup/license-hint/license-hint.component.scss';
  @import './cf-styles/ngx-guided-tour-styles.scss';

  // style snackbar and snackbar containers.
  // Moved to main to style snack-bar because it is outside of component scope.
  snack-bar-container.mat-snack-bar-container.rating-snackbar {
    max-width: unset;
    span:not(.mat-button-wrapper) {
      color: white;
    }
  }

  // custom class for quiz onboarding dialog
  .quiz-onboarding-dialog {
    padding: 12px 24px;
    .mat-dialog-container {
      padding: 40px;
    }
  }

  .cdk-overlay-container .mat-dialog-container {
    border-radius: 12px;
  }

  // Allow flex layout for analytics dialog
  .analytics-dialog-overlay .mat-dialog-container {
    display: flex;
  }

  // custom class for watch info dialog - need this here to override existing mat-dialog-container styling
  .watch-info-dialog
    .mat-dialog-container
    .umbrella-privacy-dialog
    .mat-dialog-container {
    padding-top: 12px;
  }

  // custom class for product tour dialog - need this here to override existing mat-dialog-container styling
  .product-tour-bookend-dialog .mat-dialog-container {
    padding: 0px;
    overflow: hidden;
  }

  // Prevent clipping of tooltips
  .cdk-overlay-container .mat-tooltip {
    max-width: unset;
    text-overflow: initial;
  }

  .video-analytics {
    .mat-cell,
    .mat-footer-cell,
    .mat-paginator {
      color: #2c2c2c;
    }
  }

  // custom class for watch page customization dialog
  .watch-page-customization-dialog {
    width: 50rem;
    @media screen and (min-width: 768px) {
      width: 64rem;
    }
  }
}

// Isolate submit styles so it does not interfere with old styles.
// NOTE: to apply styling see mic-page.component.ts
body.submit-styled {
  @import './submit-styles/index';
  @import './submit-styles/material/material-theme.scss';

  // custom styling for domains form in umbrella privacy dialog
  .org-input
    .mat-form-field.mat-form-field-appearance-outline
    .mat-form-field-flex {
    .mat-form-field-label {
      color: #2c2c2c;
    }

    .mat-form-field-infix {
      border: solid 1px rgba(79, 79, 79, 0.4);
      border-radius: 4px;
    }

    .mat-chip {
      background: #3ccc6c;
      font-size: 1.2rem;
      font-weight: bold;
    }
  }

  // custom styling for umbrella privacy settings dialog
  .mat-stroked-button.privacy-button {
    width: 100%;
    border: 1px solid #eee;
    border-radius: 4px;
    padding: 1.2rem 1.4rem;
  }

  .mat-menu-panel.privacy-menu-panel {
    width: 300px;
  }

  .default-setting-checkbox
    .mat-checkbox-inner-container.mat-checkbox-inner-container-no-side-margin {
    height: 1.8rem;
    width: 1.8rem;
  }
}

// Center header text for the view-date-header on the video-analytics table
.video-analytics .view-date-header > .mat-sort-header-container {
  justify-content: center;
}
