.file-list {
  .preview-image {
    background-size: cover;
  }
  md-grid-tile {
    cursor: pointer;
    outline: none;
  }
  md-grid-tile-footer {
    height: 68px;
    // background: linear-gradient(180deg, transparent, rgba(0,0,0,0.6) 30%, rgba(0,0,0,0.9));
    background: rgba(0, 0, 0, 0.7);
    figcaption {
      overflow: hidden;
      flex: 1;
      display: flex;
      flex-direction: row;
      align-items: center;
    }
    md-icon {
      color: rgba(255, 255, 255, 0.9);
    }
    h3,
    h4 {
      margin-left: 0;
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    .titles {
      margin-left: 16px;
    }
  }
  > md-content {
    min-height: 128px;
    padding: 8px;
  }

  .md-primary.md-raised.md-button {
    color: white;
  }
}
