@use 'sass:math';
@import '../functions';

.mat-checkbox {
  .mat-checkbox-inner-container {
    height: cf-unit(1 + math.div(1, 4));
    width: cf-unit(1 + math.div(1, 4));
  }

  .mat-checkbox-layout .mat-checkbox-label {
    line-height: cf-unit(1 + math.div(1, 4));
  }

  .mat-ripple {
    display: none;
  }

  .mat-checkbox-checkmark-path {
    stroke-width: cf-unit(math.div(1, 6));
    // Override theming
    stroke: #fff !important;
  }
}
