$font-url-path: '/assets/fonts/';
@function font-url($url) {
  @return url('#{$font-url-path}#{$url}');
}

$cf-font-url-path: '/assets/fonts/';
@function cf-font-url($url) {
  @return url('#{$cf-font-url-path}#{$url}');
}

// angular-material currently has now way to access theme colors directly, so we
// re-define those we need as a workaround here
$foreground-2: rgba(0, 0, 0, 0.54);
$primary-color: #ef6c00;
$accent-color: rgb(255, 64, 129);
$error-color: rgb(221, 44, 0);
