@import '../functions';
@import '../typography';

.mat-nav-list.mat-list-base {
  padding: 0;
  .mat-list-item {
    @include cf-typography(body-2);
    // Uses white color for inverted sidenav
    color: inherit;

    // Add 2px divider below item without adding pixel height to list
    &::after {
      display: block;
      height: 0;
      width: 100%;
      box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.06);
      content: '';
    }

    // Remove last divider
    &:last-child::after {
      display: none;
    }
  }
  .mat-list-icon {
    margin-right: cf-unit(0.5);
  }
}
