@import '../colors';
@import '../functions';
@import '../mixins';

.mat-sidenav-container {
  height: 100%;
  .mat-sidenav {
    width: cf-unit(12);
    border: none;
    background: $cf-gray;
    color: #fff;
    box-shadow: 1px 0px 8px rgba(0, 0, 0, 0.1);
  }

  .mat-sidenav-content {
    background: #fff;
  }

  // Override svg logo color
  cf-logo.logo svg path {
    fill: #fff;
  }
}
