#notifications {
  .md-toolbar-tools {
    font-weight: 900;
    color: white;
    height: 50px;
    max-height: 50px;

    md-icon {
      color: white;
      font-weight: 900;
      font-size: 2.5rem;
    }
  }

  .md-list-item-text.cf-custom {
    margin-left: 10px;
    padding-top: 13px;
    padding-bottom: 13px;

    p {
      line-height: 18px;
      font-weight: 400;
    }

    .first-line {
      margin-bottom: 0.5rem;
    }

    .last-line {
      color: gray;
      font-size: 12px;
    }
  }

  .unseen,
  .unseen .last-line {
    font-weight: bold;
  }
  .unseen {
    img {
      margin-left: -10px;
      height: 15px;
    }
    .md-list-item-text.cf-custom {
      margin-left: 5px;
    }
  }
}
