.livepreview {
  overflow: hidden;
  .live-preview-canvas {
    background: #aaa;
    position: relative;
    display: flex;
    overflow: hidden;
    video {
      display: block;
      object-fit: contain;
      &.camera {
        width: 100%;
        max-height: 100%;
      }
    }
  }

  .ctrls {
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 48px;
    > * {
      margin: 0;
      flex: 1;
    }
    .recording-duration {
      width: 80px;
      flex: none;
      text-align: center;
      align-self: center;
    }
  }
  @media (max-width: 280px) {
    .hide-small {
      display: none;
    }
  }
}

@include keyframes(record-icon-anim) {
  from {
    opacity: 0.2;
  }

  to {
    opacity: 1;
  }
}
