cf-auto-stop-dropdown {
  min-height: 48px;
  .option {
    margin-left: 30px;
    padding-bottom: 15px;
  }
  label {
    padding-right: 10px;
  }
  md-select {
    margin: 0 0;
  }
}

md-menu-content.auto-stop-dropdown {
  md-menu-item.disabled {
    color: gray;
  }
  .no-padding {
    padding: 0;
  }
  .height-100 {
    height: 100%;
  }
  .bottom-border {
    border: 0;
    border-bottom: 1px solid lightgray;
  }
  md-menu-item > input {
    height: 100%;
  }
}
