body,
html {
  margin: 0;
  background-color: white !important;
}

html {
  font-size: 62.5%;
  line-height: 1.4;
  &.popup {
    font-size: 59.5%;
  }
}

// Override angular-material defaults for popup.
html.popup,
body.popup {
  min-height: unset;
  height: unset;
  overflow: hidden !important;
}

.ng-cloak {
  display: none;
}

a {
  cursor: pointer;
  color: inherit;
  text-decoration: underline;
}

body {
  cursor: default;
  font-family: inherit;
  font-size: 1.6rem;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;

  // Screencastify branding
  font-family: 'Helvetica', sans-serif;
  font-weight: 400;
  color: #555;

  h1,
  h2 {
    font-family: 'Source Sans Pro', sans-serif;
    letter-spacing: -0.05rem;
  }
}

* {
  box-sizing: border-box;
  outline: none;
}

body.popup {
  padding-bottom: 0;
  min-height: 200px;
  min-width: 325px;
  max-height: 700px;

  // Hack for 1.1.0rc2 toast sizing bug
  md-toast .md-toast-content {
    height: unset;
    // for multi-line text
    padding: 6px 18px;
  }
}

body.app {
  overflow: hidden;
  .view {
    height: 100%;
    > * {
      overflow: auto;
    }
  }
}

// Restore pre v48 flex/min-size behavior by default.
.layout-row {
  min-height: 0;
}

.layout-column {
  min-width: 0;
}

// allow multiple buttons in menu items
md-menu-item > .md-icon-button {
  text-align: center;
  md-icon {
    margin: auto;
  }
}

// Disable text selection in checkboxes
md-checkbox .md-label {
  user-select: none;
}

.cf-padding {
  padding: 8px;
}

md-menu-item .md-caption {
  // distance for help text in menu items
  margin-left: 4px;
}

md-dialog.cf-dynamic {
  // avoid re-centering for dynamic dialogs
  margin-top: 64px;
  max-height: calc(100% - 64px);
  align-self: flex-start;
}

.micstream {
  .head-icon {
    align-self: center;
    margin-right: 24px;
    md-icon {
      font-size: 64px;
      height: 64px;
      width: 64px;
    }
  }
  max-width: 600px;
  p {
    font-size: 18px;
  }
  .helpscreen {
    margin-top: 20px;
  }
}

.dropdown-menu.open {
  display: block;
}

.recorder-state {
  @each $state in inactive, paused, recording {
    &:not(.#{$state}) .show-on-#{$state} {
      display: none;
    }
  }
}

.ng-animate.item:not(.left):not(.right) {
  -webkit-transition: 0s ease-in-out left;
  transition: 0s ease-in-out left;
}

.menu-heading {
  padding-left: 8px;
  padding-right: 8px;
  @include md-subhead();
}

// HACK for https://github.com/angular/material/issues/8335
md-menu-item > *,
md-menu-item > .md-button {
  min-width: 100%;
}

.md-title {
  font-weight: 700;
  font-size: 2.75rem;
  color: rgba(79, 79, 79, 0.87);
  letter-spacing: -0.05rem;
}

md-dialog {
  max-width: 600px;

  .md-button.md-cancel-button {
    color: #555 !important;
  }

  button.md-confirm-button {
    color: white !important;
    background-color: #ff8282 !important;
    font-weight: 700 !important;

    md-icon {
      color: white !important;
    }

    &:hover {
      background-color: #c85255 !important;
    }
  }

  button.md-confirm-button:hover {
    background-color: #c85255 !important;
  }

  md-dialog-actions {
    border: none;

    .md-button {
      text-transform: none;
      font-size: 1.5rem;
      font-weight: 700;
      padding: 0 16px;
      border-radius: 4px;
    }
  }
}

.popup md-dialog-content {
  padding: 16px;
}

body:not(.popup) md-dialog {
  md-dialog-actions {
    padding-right: 24px;
  }

  .md-dialog-content-body {
    font-size: 1.75rem;
    color: rgba(79, 79, 79, 0.87);
    font-weight: 300;
  }
}

.md-select-menu-container.device-chooser {
  left: 10px !important;
  right: 10px !important;
}

// fix for chrome extension views expanding differently as of v94
cf2-sidenav {
  display: block;
}
ui-view {
  display: block;
}
cf-recorder {
  display: block;
}
cf-recorder-recording {
  display: block;
}
cf-record-ctrl-buttons {
  display: block;
}
cf-recorder-recording .ctrlButtons .ctrl-buttons-row {
  display: flex;
  flex-direction: row;
}
.recorder #recorder-wrapper {
  display: block;
}
.recorder cf-recorder-recording .ctrlButtons {
  display: block;
}
